:root {
  --color-debug: red;
  --font-color-gray-0: #1a1a1a;
  --font-color-gray: #333;
  --font-color-gray-2: #8a8a8a;
  --font-color-gray-3: #dbdbdb;
  --color-superlight: #fff;
  --color-superlight-faded: #ffffff80;
  --color-hr-purple: #ae00ff;
  --color-hr-purple-2: #cf75f8;
  --color-hr-purple-3: #e4b3fb;
  --color-secondary: #df86e7;
  --color-tertiary: #160a59;
  --color-tertiary-faded: #160a5980;
  --color-hr-blue-A: #0c002c;
  --color-hr-blue-A-faded: #0c002c80;
  --color-hr-blue: #40f;
  --color-hr-blue-2: #652ffa;
  --color-hr-blue-3: #a384f8;
  --color-tertiary-light: #6666e5;
  --color-tertiary-lightB: #b1b1ee;
  --color-hr-lightblue-C: #672de5;
  --color-hr-lightblue-B: #4a4aff;
  --color-hr-lightblue-A: #1d88ff;
  --color-hr-lightblue: #0af;
  --color-hr-lightblue-2: #6ac9f9;
  --color-hr-lightblue-3: #a5e0fd;
  --color-hr-lightgreen: #0f0;
  --color-hr-lightgreen-2: #70fa70;
  --color-hr-lightgreen-3: #a9f7a9;
  --color-green-light: #53c153;
  --color-hr-yellow: #ffbc00;
  --color-hr-yellow-2: #fdcf4e;
  --color-hr-yellow-3: #fae4a5;
  --color-hr-orange: #f98f7f;
  --color-hr-orange-2: #d05f26;
  --color-hr-orange-3: #f2976a;
  --color-hr-orange-4: #e273a9;
  --color-main-background: var(--color-superlight);
  --color-main-background-faded: var(--color-superlight-faded);
  --color-alternative-background: var(--font-color-gray-3);
  --color-nav-background: var(--color-superlight);
  --color-nav-text-default: var(--color-hr-orange-3);
  --color-nav-text-active: var(--color-hr-orange-2);
  --color-footer-background: var(--color-superlight);
  --color-header-text: var(--font-color-gray-3);
  --color-header-text-inverse: var(--color-superlight);
  --color-submenu-default: var(--color-hr-blue-3);
  --color-submenu-active: var(--color-hr-purple-2);
  --color-anchor-default: var(--color-hr-blue-3);
  --color-anchor-active: var(--color-hr-purple-2);
  --color-text-faded: var(--font-color-gray-2);
  --color-text-default: var(--font-color-gray-0);
  --color-text-highlight: var(--color-hr-orange-2);
  --color-text-special-1: var(--color-hr-orange);
  --color-text-special-2: var(--color-hr-yellow);
  --color-text-special-3: var(--color-hr-lightblue);
  --color-card-front: var(--color-hr-blue-3);
  --color-card-front-header: var(--font-color-gray-0);
  --color-card-back: var(--font-color-gray-0);
  --color-card-back-header: var(--color-hr-blue-3);
  --color-btn-1-know: transparent;
  --color-btn-1-know-text: var(--font-color-gray-0);
  --color-btn-2-next: var(--color-hr-purple-2);
  --color-btn-2-next-text: var(--font-color-gray-0);
  --color-btn-3-back: var(--color-tertiary-lightB);
  --color-btn-3-back-hover: var(--color-hr-orange);
  --color-btn-3-back-text: var(--color-superlight);
  --color-btn-4-email: var(--color-hr-lightblue);
  --color-btn-5-whats: var(--color-hr-lightblue-A);
  --color-btn-6-news: var(--color-hr-lightblue-B);
  --color-btn-7-green: var(--color-hr-lightgreen-2);
  --color-icon-faded: var(--font-color-gray-2);
  --color-icon-default: var(--color-hr-purple-3);
  --color-icon-active: var(--color-hr-purple-2);
  --main-nav-height: 8vh;
  --mobile-nav-height: 8vh;
  --secondary-nav-height: 87vh;
  --footer-height: 5vh;
  --viewport-height: calc(100vh - var(--main-nav-height)  - var(--footer-height));
  --viewport-height-mobile: calc(100vh - var(--mobile-nav-height));
  --scrollbar-width: 7px;
  --general-width: calc(100vw - var(--scrollbar-width));
  --general-h-spacer: 1vw;
  --general-v-spacer: 1vh;
  --card-width: 30vw;
  --card-height: 50vh;
  --card-width-mobile: 80vw;
  --card-width-fullscreen: 27vw;
  --scroll-item-ammount: 10;
}

.darkmode {
  --color-main-background: var(--color-tertiary);
  --color-main-background-faded: var(--color-tertiary-faded);
  --color-alternative-background: var(--color-tertiary-lightB);
  --color-nav-background: var(--color-tertiary);
  --color-nav-text-default: var(--color-superlight);
  --color-nav-text-active: var(--color-hr-blue-3);
  --color-footer-background: var(--color-tertiary);
  --color-header-text: var(--color-superlight);
  --color-header-text-inverse: var(--color-superlight);
  --color-submenu-default: var(--color-hr-blue-3);
  --color-submenu-active: var(--color-hr-purple-2);
  --color-anchor-default: var(--color-hr-blue-3);
  --color-anchor-active: var(--color-hr-purple-2);
  --color-text-faded: var(--font-color-gray-2);
  --color-text-default: var(--color-superlight);
  --color-text-highlight: var(--color-hr-purple-2);
  --color-text-special-1: var(--color-hr-orange);
  --color-text-special-2: var(--color-hr-yellow);
  --color-text-special-3: var(--color-hr-lightblue);
  --color-card-front: var(--color-hr-lightblue-C);
  --color-card-front-header: var(--font-color-gray-0);
  --color-card-back: var(--font-color-gray-0);
  --color-card-back-header: var(--color-hr-blue-3);
  --color-btn-1-know: transparent;
  --color-btn-1-know-text: var(--font-color-gray-0);
  --color-btn-2-next: var(--color-hr-purple-2);
  --color-btn-2-next-text: var(--font-color-gray-0);
  --color-btn-3-back: var(--color-hr-purple-3);
  --color-btn-3-back-hover: var(--color-hr-orange);
  --color-btn-3-back-text: var(--color-superlight);
  --color-btn-4-email: var(--color-hr-lightblue);
  --color-btn-5-whats: var(--color-hr-lightblue-A);
  --color-btn-6-news: var(--color-hr-lightblue-B);
  --color-btn-7-green: var(--color-hr-lightgreen-2);
  --color-icon-faded: var(--font-color-gray-2);
  --color-icon-default: var(--color-hr-purple-3);
  --color-icon-active: var(--color-hr-purple-2);
}

::-webkit-scrollbar {
  width: var(--scrollbar-width);
  height: var(--scrollbar-width);
}

::-webkit-scrollbar-thumb {
  background: var(--color-nav-text-active);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  cursor: grab;
}

::-webkit-scrollbar-track {
  margin-block: var(--main-nav-height) var(--footer-height);
  margin-inline: var(--main-nav-height);
}

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

a {
  text-decoration: none;
  list-style: none;
}

html {
  scroll-snap-type: block mandatory;
  scroll-behavior: smooth;
  min-height: 100vh;
}

body {
  min-height: 100vh;
  background: var(--color-main-background);
  flex-direction: column;
  justify-content: flex-start;
  place-items: center;
  display: flex;
  overflow-x: hidden;
}

.body-overflow {
  margin-right: var(--scrollbar-width);
  overflow: hidden;
}

.loader-wrapper {
  z-index: 10;
  width: 100vw;
  height: 100vh;
  background-color: var(--color-main-background);
  flex-direction: column;
  place-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loader {
  width: 30px;
  height: 30px;
  border: 4px solid var(--color-icon-default);
  animation: 2s infinite loader;
  display: inline-block;
  position: relative;
}

.loader-inner {
  vertical-align: top;
  width: 100%;
  background-color: var(--color-icon-default);
  animation: 2s ease-in infinite loader-inner;
  display: inline-block;
}

@keyframes loader {
  0% {
    transform: rotate(0);
  }

  25% {
    transform: rotate(180deg);
  }

  50% {
    transform: rotate(180deg);
  }

  75% {
    transform: rotate(360deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes loader-inner {
  0% {
    height: 0%;
  }

  25% {
    height: 0%;
  }

  50% {
    height: 100%;
  }

  75% {
    height: 100%;
  }

  100% {
    height: 0%;
  }
}

.lgpd {
  z-index: 10;
  width: 100vw;
  height: 100vh;
  background-color: var(--color-main-background);
  color: var(--color-text-default);
  opacity: 0;
  pointer-events: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: opacity .5s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.lgpd p {
  text-align: center;
  width: -moz-fit-content;
  width: fit-content;
}

.lgpd a {
  color: var(--color-hr-purple-2);
  text-transform: initial;
  text-decoration: underline;
}

.button-lgpd {
  width: 300px;
  height: 30px;
  border: 1px solid var(--color-text-default);
  color: var(--color-text-default);
  background-color: #0000;
  border-radius: 10px;
  margin-top: 2vh;
  transition: background-color .2s ease-in-out;
}

.button-lgpd:hover {
  cursor: pointer;
  background-color: var(--color-text-default);
  color: var(--color-main-background);
}

.lgpd-inactive {
  opacity: 0;
  pointer-events: none;
}

.lgpd-active {
  opacity: 1;
  pointer-events: all;
  transform: none;
}

.nav-container {
  z-index: 4;
  width: 100vw;
  height: var(--main-nav-height);
  background-image: linear-gradient(var(--color-main-background) 80%, var(--color-main-background-faded) 90%, transparent 100%);
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-inline: 6vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.nav-primary {
  z-index: 4;
  width: 80%;
  height: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav-item-active {
  transition: color .3s ease-in-out;
  color: var(--color-nav-text-active) !important;
}

.nav-item-active:before {
  width: 100% !important;
  background-color: var(--color-nav-text-active) !important;
  transition: width .3s ease-in-out !important;
}

.nav-primary ul, .nav-primary li, .nav-primary a {
  width: auto;
  height: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav-primary a, .nav-secondary a {
  text-align: center;
  text-transform: uppercase;
  color: var(--color-nav-text-default);
  height: 60%;
  margin-inline: 1.2vw;
  font-size: 1.2vw;
  position: relative;
}

.nav-primary a:before, .nav-secondary a:before {
  content: "";
  width: 0;
  height: .3vw;
  background-color: var(--color-nav-text-default);
  transform-origin: center;
  transition: background-color .3s, width .3s;
  display: block;
  position: absolute;
  bottom: 0;
  left: auto;
  right: auto;
}

.nav-primary a:hover:before, .nav-primary a:focus:before, .nav-secondary a:hover:before, .nav-secondary a:focus:before {
  width: 100%;
  background-color: var(--color-nav-text-default);
  transition: width .3s;
}

.nav-secondary {
  z-index: 2;
  height: 100vh;
  width: 100vw;
  padding-top: var(--main-nav-height);
  padding-bottom: var(--footer-height);
  background-color: var(--color-main-background);
  flex-direction: column;
  justify-content: center;
  place-items: center;
  transition: transform 1s ease-in-out;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(100vw);
}

.nav-secondary ul, .nav-secondary li, .nav-secondary a {
  width: 100%;
  height: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav-secondary ul {
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  place-items: center;
  display: flex;
}

.nav-secondary a {
  width: auto;
  height: 25%;
  font-size: 1.4vw !important;
}

.nav-secondary-active {
  transform: translateX(0);
}

.nav-secondary-sub-1 {
  z-index: 2;
  top: calc(var(--main-nav-height)  + 3vh);
  height: auto;
  min-width: 30%;
  border: 1px solid var(--color-text-default);
  background-color: var(--color-main-background);
  pointer-events: none;
  border-radius: 10px;
  flex-direction: column;
  place-content: center;
  align-items: center;
  padding: 10px;
  transition: transform .5s ease-in-out;
  display: flex;
  position: absolute;
  overflow: hidden;
  transform: translateY(100vh);
}

.config-header {
  width: 90%;
  flex-direction: row;
  place-content: center space-between;
  align-items: center;
  display: flex;
}

.nav-secondary-sub-1 h4 {
  color: var(--color-text-default);
  margin-block: 20px;
  font-size: 24px;
}

.nav-secondary-sub-1 ul {
  color: var(--color-text-default);
  flex-direction: column;
  place-content: center;
  align-items: flex-start;
  display: flex;
}

.nav-secondary-sub-1 svg {
  fill: var(--color-nav-text-active);
  margin-right: 20px;
}

.nav-secondary-sub-1 .config-item {
  border: 1px solid var(--color-text-default);
  border-radius: 5px;
  flex-direction: row;
  place-content: center space-between;
  align-items: center;
  margin-block: 5px;
  padding: 10px;
  display: flex;
}

.item-header {
  width: 100%;
  flex-direction: row;
  align-content: center;
  align-items: center;
  display: flex;
}

.nav-secondary-sub-1 .config-sub-list {
  width: min-content;
  flex-flow: column;
  place-content: center;
  align-items: flex-end;
  display: flex;
}

.nav-secondary-sub-1 .config-sub-item {
  width: max-content;
  flex-direction: row;
  place-content: center flex-end;
  align-items: center;
  padding: 3px;
  display: flex;
}

.nav-secondary-sub-1 button {
  border: 1px solid var(--color-text-default);
  color: var(--color-text-default);
  width: 100px;
  background-color: #0000;
  border-radius: 5px;
  margin-left: 10px;
  padding: 3px;
}

.nav-secondary-sub-1 button:hover {
  background-color: var(--color-nav-text-active);
  border: 1px solid var(--color-nav-text-active);
  color: var(--color-main-background);
  cursor: pointer;
}

.config-div-active {
  pointer-events: all;
  transform: translateY(0);
}

.desenvolvido-por {
  color: var(--color-text-default);
  width: 25%;
  height: 15%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  display: flex;
}

.desenvolvido-por a {
  width: auto;
  height: 35px;
  margin-inline: 5px;
  font-size: 16px !important;
}

.logo-container {
  z-index: 4;
  width: 25%;
  height: 100%;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.logo-placeholder {
  width: 4vh;
  height: 4vh;
  transition: transform .3s ease-in-out;
  transform: scale(1.5);
}

.logo-placeholder:hover {
  transform: scale(1.6);
}

.logo-placeholder svg {
  width: 100%;
  height: 100%;
  fill: var(--color-nav-text-active);
}

.logo-text {
  color: var(--color-text-default);
  margin-inline: 1vw;
  font-size: 1.4vw;
  font-weight: 600;
  transition: color .3s, font-size .3s, font-weight .3s;
}

.logo-text:hover {
  color: var(--color-nav-text-active);
  cursor: pointer;
  font-size: 1.5vw;
  font-weight: 300;
}

.menu-div {
  z-index: 4;
  width: 25%;
  height: 100%;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.darkmode-switch {
  width: 50%;
  height: 100%;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.darkmode-switch svg {
  fill: var(--color-nav-text-default);
  width: 48px;
  height: 48px;
  flex-shrink: 0;
  transition: fill .3s ease-in-out, transform .3s ease-in-out;
  transform: scale(.5);
}

.darkmode-switch svg:hover {
  cursor: pointer;
  fill: var(--color-nav-text-active);
  transform: scale(.65);
}

.lang-selector {
  max-width: 50%;
  height: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-inline: 1vw;
  display: flex;
}

.lang-selector a {
  margin-inline: 6px;
  font-size: 1vw;
  transition: color .3s ease-in-out;
}

.lang-selector a:hover {
  color: var(--color-nav-text-active);
}

.lang-selector svg {
  fill: var(--color-text-default);
  width: 20px;
  height: 20px;
  flex-shrink: 0;
}

.lang-divider {
  width: 2px;
  height: 16px;
  background-color: var(--color-text-default);
}

.lang-selected {
  pointer-events: none;
  color: var(--color-nav-text-active);
}

.lang-unselected {
  color: var(--color-nav-text-default);
}

.burger-class {
  width: 5vh;
  height: 5vh;
  cursor: pointer;
  flex-direction: column;
  place-content: center space-evenly;
  align-items: center;
  transition: transform .3s ease-in-out;
  display: flex;
  position: relative;
  transform: scale(.9);
}

.burger-class:hover {
  transform: scale(1.1);
}

.burger-class > * {
  width: 3vh;
  height: 3px;
  background-color: var(--color-nav-text-default);
  border-radius: 2px;
  transition: transform 1s ease-in-out, opacity .8s ease-in-out, background-color .2s ease-in-out;
  position: absolute;
}

.burger-class :nth-child(1) {
  transform: translateY(-10px);
}

.burger-class :nth-child(2) {
  opacity: 1;
}

.burger-class :nth-child(3) {
  transform: translateY(10px);
}

.burger-class:hover > * {
  background-color: var(--color-nav-text-active);
}

.burger-active :nth-child(1) {
  transform: rotate(-45deg);
}

.burger-active :nth-child(2) {
  opacity: 0;
  transform: translateX(50px);
}

.burger-active :nth-child(3) {
  transform: rotate(45deg);
}

.close-class {
  width: 5vh;
  height: 5vh;
  cursor: pointer;
  flex-direction: column;
  place-content: center space-evenly;
  align-items: center;
  transition: transform .3s ease-in-out;
  display: flex;
  position: relative;
  transform: scale(.9);
}

.close-class:hover {
  transform: scale(1.1);
}

.close-class > * {
  width: 3vh;
  height: 3px;
  background-color: var(--color-nav-text-default);
  border-radius: 2px;
  transition: transform 1s ease-in-out, opacity .8s ease-in-out, background-color .2s ease-in-out;
  position: absolute;
}

.close-class :nth-child(1) {
  transform: rotate(-45deg);
}

.close-class :nth-child(2) {
  opacity: 0;
}

.close-class :nth-child(3) {
  transform: rotate(45deg);
}

.close-class:hover > * {
  background-color: var(--color-nav-text-active);
}

main {
  height: auto;
  width: 100%;
  margin-top: var(--main-nav-height);
}

.section-divider {
  z-index: -1;
  height: 300px;
  width: 100%;
  margin-block: var(--main-nav-height);
  background-color: #0000;
  flex-direction: column;
  place-content: center;
  align-items: center;
  display: flex;
}

.section-divider svg {
  z-index: -1;
  width: auto;
  height: 80%;
}

.svg-group {
  fill: var(--color-nav-text-default) !important;
}

section {
  width: 100%;
  height: var(--viewport-height);
  margin-bottom: var(--main-nav-height);
  scroll-behavior: smooth;
  scroll-snap-align: start;
  border-radius: 20px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  scroll-margin-block-start: calc(var(--main-nav-height)  + 1px);
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: inset 0 0 1px 2px #000;
}

.section-text {
  z-index: 3;
  text-align: left;
  top: 0;
  left: var(--main-nav-height);
  width: 50%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  display: flex;
  position: absolute;
}

.section-text h1, .section-text h2 {
  color: var(--color-text-default);
  width: auto;
  height: auto;
  flex-shrink: 0;
}

.section-text h1 {
  font-size: 10vw;
  font-weight: 700;
}

.section-text h2 {
  opacity: .6;
  font-size: 3vw;
  font-weight: 500;
}

.video-controller-container {
  display: none;
}

.video-controller-container button {
